export default {
  props: ['barsList', 'enter'],
  components: {

  },
  data () {
    return {
      title: '你好',
      page: '5'
    }
  },
  mounted () {
  },
  methods: {
    pageToBarInfo () {
      this.$router.push({path: '/AgentBarInfo'})
    }
  },
  filters: {
    distance (value) {
      var d = Number(value / 1000).toFixed(2) + 'km'
      return d
    }
  }
}



